
<template>
  <div class="container shifted"><center>
  <table id="artTable"><tbody>
    <tr v-for="row in rows">
      <td>
        <p><img :src=row.ImageUrl style="object-fit: cover; object-position: center;" width="100px" height="100px" align="right"><p class=small>{{row.SourceName}}</p><a :href=row.SourceUrl class=medium>{{row.Title}}</a><p class=micro>{{timeAgo(row.PublishAt)}}</p>
        <a :href=row.SourceUrl class=small>{{row.Detail}}</a>
      </td>
    </tr>
  </tbody></table>
  </center></div>
</template>

<script>
import Vue from 'vue';
import Amplify, { API } from 'aws-amplify';

export default {
  name: 'Home',
  data () {
    return {
      message: 'Digest of programmer',
      rows: [
      ]
    }
  },

  async created() {
    const currentUser = await this.$Amplify.Auth.currentUserInfo();

    API.post('swa', '/swaListArts', {'body': {'ApiVer': 0, 'After': 0, 'Lang': 'en'}}).then(response => {
      console.log(response);
      this.rows = response.Articles;
    }).catch(error => {
      console.log(error)
    });
  },

  methods: {
    timeAgo: function(date) {
      var diff = Math.round((new Date() - date) / 1000)
      const times = [["second", 1], ["minute", 60], ["hour", 3600], ["day", 86400], ["week", 604800], ["month", 2592000], ["year", 31536000]]
      for (var t = 0; t < times.length; t++) {
        if (diff < times[t][1]) {
            if (t == 0) {
                return "Just now"
            } else {
                diff = Math.round(diff / times[t - 1][1])
                return diff + " " + times[t - 1][0] + (diff == 1?" ago":"s ago")
            }
        }
      }
    }
  }
}

</script>



